.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: 'Inconsolata', monospace;
  /* background-color: black; */

}
.title {
  font-size: calc(10px + 2vmin);
  margin-top: 10px;
  font-weight: bold;
  color: #0032bc;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.Layout{
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: solid; */
  /* border-radius: 10px; */
  padding: 20px;
  margin-top: 2%;
  width: auto;
}

.Side {
  margin: 5px;
  border: solid 3px;
  border-radius: 3px;
  background-color: black;
}

/* Rows */
.topRow {
  display: flex;
  flex-direction: row;
}

.midRow {
  display: flex;
  flex-direction: row;
}

.botRow {
  display: flex;
  flex-direction: row;
}

.tile {
  border-radius: 5px;
  border: solid 2px;
  margin:0px;
  height: 6vh;
  width: 6vh;

  font-family: 'Inconsolata', monospace;
  font-weight: bold;
  font-size: 15px;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;

  transition-duration: 0.1s;
}

/* Tiles Colors */
.tile#B {
  background-color: #0b2afb;
}
.tile#R {
  background-color: #fc0d1b;
}
.tile#Y {
  background-color: #ebe932;
}
.tile#W {
  background-color: #ffffff;
}
.tile#G {
  background-color: #3cd947;
}
.tile#O {
  background-color: #fd8824;
}
