button {
  outline: none;
  font-family: 'Inconsolata', monospace;
  /* font-weight: bold; */
  background-color: white;
  color: black;
  border: 2px solid black;
  padding: 4px;
  padding-right:15px;
  padding-left:15px;
  font-size: 16px;
  margin-right: 5px;
  margin-left: 5px;
  margin-bottom: 10px;
}

button:hover {
  background-color: #0b2afb;
  /* font-weight: bold; */
  color: rgb(255, 251, 0);
  /* transition-duration: 0.4s; */
  cursor: pointer;
  /* background-color: #0032bc; */
  border-color: #0b2afb;
  /* border-color:rgb(255, 251, 0); */
}

button:active{
  background-color: rgb(255, 251, 0);
  border-color: rgb(255, 251, 0);
  color: black;
}

button.logging {
  background-color: rgb(255, 251, 0);
  border-color: rgb(255, 251, 0);
}
button.logging:hover {
  background-color: #0b2afb;
  /* font-weight: bold; */
